


import React, { useState, useEffect } from 'react';
import { Typography, Button } from '@mui/material';
import axios from 'axios';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import { Popconfirm, message, Select } from 'antd'

import Box from '@mui/material/Box';
import { Link } from 'react-router-dom';
import { styled, alpha } from '@mui/material/styles';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';

import Page from '../../../components/Page';

const PageTitle = styled('div')(({ theme }) => ({
	backgroundColor: `${alpha(theme.palette.primary.main, 0.08)}`
}));


export default function EcommerceProductList() {
	const [data, setData] = useState([]);
	const [change, SetChange] = useState(false);
	const [refresh, SetRefresh] = useState(false);
	const [statusId, setStatusId] = useState("0");
	message.config({ top: 100, duration: 5, });

	const renderDetailsButton = (params) => {
		return (
			<>
				<Link to={`/dashboard/esetting/edit/${params.row.id}`} style={{ textDecoration: 'none' }}>
					<Button
						variant="outlined"
						color="primary"
						size="small"
						style={{ marginLeft: 16 }}
					>
						Edit
					</Button>
				</Link>
				<Popconfirm
					title="Delete the task"
					description="Are you sure to delete this account?"
					onConfirm={() => handleDelete(params)}
					okText="Yes"
					cancelText="No"
				>
					<Button
						variant="outlined"
						color="primary"
						size="small"
						style={{ marginLeft: 16 }}
					>
						Delete
					</Button>
				</Popconfirm>
			</>
		)
	}

	const changeStatus = (val) => {
		var checker = 0;
		for (const i in data) {
			if (data[i].id === statusId && data[i].status === "1") {
				checker = checker + 1;
			}
		}
		
		axios.post(`${process.env.REACT_APP_SERVER_URL}/changeEsettingById`, { id: statusId, val: val })
			.then((res) => {
				SetRefresh(!refresh)
			})
	}

	const renderStatus = (params) => {
		return (
			<>
				<Select
					value={params.row.status}
					style={{
						width: 120,
					}}
					onClick={() => setStatusId(params.row.id)}
					onChange={changeStatus}
					options={[
						{
							value: '1',
							label: 'Active',
						},
						{
							value: '0',
							label: 'Disable',
						},
					]}
				/>
				{/* {params.row.status === '1' ? 'Active' : 'Disable'} */}
			</>
		)
	}

	const columns = [
		{ field: 'email', headerName: 'Email', width: 250 },
		{ field: 'status', headerName: 'Status', renderCell: renderStatus, width: 200 },
		{ field: 'action', headerName: 'Action', renderCell: renderDetailsButton, sortable: false, width: 200 }
	];

	const handleDelete = (e) => {
		console.log(e.row.id)
		axios.post(`${process.env.REACT_APP_SERVER_URL}/deletemail`, { id: e.row.id })
			.then((res) => {
				if (res.data.flag === 'success') {
					message.config({ top: 100, duration: 5, });
					message.success('Successfully deleted.');
					SetChange(!change)
				}
			})
	}

	useEffect(() => {
		setTimeout(() => {
			SetRefresh(true)
		}, 200);

		return () => {
			axios.post(`${process.env.REACT_APP_SERVER_URL}/getemail`)
				.then((res) => {
					setData(res.data.data);
				})
		}
	}, [change, refresh]);

	return (
		<>
			<Page title="All Users">
				<PageTitle>
					<Typography
						color="text.primary"
						sx={{
							fontSize: '23px',
							fontWeight: 'bold',
							padding: '10px',
							mb: '20px'
						}}>
						Email accounts
					</Typography>
				</PageTitle>

				<Box sx={{ px: '20px' }}>
					<div style={{ marginTop: '20px', marginBottom: '20px', display: 'flex' }}>
						<div style={{ flex: '1 1 0%', textAlign: 'right' }}>
							<Link to="/dashboard/esetting/add" style={{ textDecoration: 'none' }}>
								<Button className='btn_info' variant="contained" startIcon={<AddCircleOutlineIcon />} sx={{ mb: '10px' }}> Add Email Account </Button>
							</Link>
						</div>
					</div>
				</Box>
				<div style={{ height: '700px', width: '100%' }}>
					<DataGrid
						rows={data}
						columns={columns}
						pageSize={10}
						rowsPerPageOptions={[10]}
						components={{
							Toolbar: GridToolbar,
						}}
					/>
				</div>
			</Page>
		</>
	);
}

